@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
body {
  font-family: "Roboto", sans-serif;
  background-color: #6c001d;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

input {
  all: unset;
}

/*  */

.App {
  margin: 0;
}

.wrapper {
  width: 80%;
  margin: auto;
}

.container::after {
  content: "";
  display: block;
  height: 1px;
  background-color: #fff;
  width: 80%;
  margin: auto;
  margin-bottom: 4rem;
}

.contact {
  width: fit-content;
  margin: auto;
  font-size: 5rem;
  margin-bottom: 4rem;
}

.contact:hover {
  color: #fff;
  cursor: pointer;
}

@media (max-width: 1280px) {
  .wrapper {
    width: 100%;
  }
  main {
    padding: 5rem;
  }
}

@media (max-width: 750px) {
  main {
    padding: 0;
  }
}
